.section {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.navbar {
    background: rgba(0, 0, 0, 0.6);
    opacity: 0.6;
}

.navbar-brand img{
    height: 4rem;
}

.nav-link {
    height: 1rem;
}

.video-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1000;
    overflow: hidden;
}

.video1 {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
}

.content {
    position: absolute;
    width: 100%;
    min-height: 100%;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.4);
}

.content h1 {
    text-align: center;
    font-size: 55px;
    text-transform: uppercase;
    font-weight: 300;
    color: #fff;
    padding-top: 20%;
    margin-bottom: 10px;
}

.content h2 {
    font-size: 25px;
    color: #aaa;
}

.content p {
    text-align: center;
    font-size: 15px;
    letter-spacing: 3px;
    color: #aaa;
}

.content a {
    font-size: 17px;
    color: #aaa;
    border-radius: 10%;
    border-color: #aaa
}

@media only screen and (max-width: 760px) {
    .content h1 {
        text-align: center;
        font-size: 35px;
        text-transform: uppercase;
        font-weight: 300;
        color: #fff;
        padding-top: 20%;
        margin-top: 20%;
        margin-bottom: 10px;
    }
  
    .content p {
        text-align: center;
        font-size: 10px;
        letter-spacing: 3px;
        color: #aaa;
    }
}

@media only screen and (max-width: 600px) {
    .content h1 {
        text-align: center;
        font-size: 35px;
        text-transform: uppercase;
        font-weight: 300;
        color: #fff;
        padding-top: 30%;
        margin-top: 40%;
        margin-bottom: 10px;
    }
  
    .content p {
        text-align: center;
        font-size: 10px;
        letter-spacing: 3px;
        color: #aaa;
    }
}